@import "./componentsStyling/settingsButton.css";
@import "./componentsStyling/playPauseButtonStyling.css";
* {
  margin: 0;
  padding: 0;
}
body {
  background-color: var(--main-bg-color);
  color: var(--comp);
  display: flex;
  width: 100vw;
  height: 100vh;
  align-self: center;
  align-content: center;
}

main {
  display: flex;
  background-color: var(--main-bg-color);
  width: 100vw;
  height: 100vh;
  justify-content: center;
}

.timerContainer {
  background-color: var(--element-bg);
  width: 25%;
  padding: 2rem;
  align-self: center;
  border-radius: 50px;
  box-shadow: 20px 20px 60px #becad7, -20px -20px 60px #e0eefd;
}

.settingsContainer{
  background-color: var(--element-bg);
  width: 25%;
  padding: 2rem;
  align-self: center;
  border-radius: 50px;
  box-shadow: 20px 20px 60px #becad7, -20px -20px 60px #e0eefd;
}

