:root{
--main-bg-color:#cfdcea;
--element-bg:#e3edf7;
--comp: lightblue;
font-family: Arial, Helvetica, sans-serif;
}
/* putting the button on the top corner right */
.settingsButtonContainer {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

/* Syling the button element */
.settingsButtonContainer button {
  background-color: transparent;
  color: var(--comp);
  border: none;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  box-shadow: 20px 20px 60px #becad7, -20px -20px 60px #e0eefd;
}

/* adding some padding to the icon */
.h-5 {
  background-color: transparent;
  padding: 10%;
}

/* hovered button */
.settingsButtonContainer button :hover {
  box-shadow: -12px -23px 55px rgba(255, 255, 255, 0.5) inset;
  -webkit-box-shadow: -12px -23px 55px rgba(255, 255, 255, 0.5) inset;
  -moz-box-shadow: -12px -23px 55px rgba(255, 255, 255, 0.5) inset;
  background-color: transparent;
  cursor: pointer;
  border-radius: 50%;
}

/* CLICKED BUTTON */
.settingsButtonContainer :active {
  background-color: var(--comp);
  color: var(--element-bg);
  cursor: pointer;
  box-shadow: 20px 20px 60px #becad7, -20px -20px 60px #e0eefd;
  border-radius: 50%;

}

.playPauseButtonContainer button {
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  width: 60px;
  height: 60px;
  
  color: rgb(0, 0, 0);
  box-shadow: 20px 20px 60px #becad7, -20px -20px 60px #e0eefd;
 
}


